@charset "utf-8";
@import './default.css';
@import './colorset.css';
@font-face {
   font-family: 'Pretendard-Regular';
   src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
   font-weight: 400;
   font-style: normal;
}
html,body{width:100%;min-width:320px;height:100vh;font-size:16px;}
body{font-family:Helvetica,'Pretendard-Regular','NanumSquareRound',sans-serif,Arial;}
button{display:flex;justify-content:center;align-items:center;padding:10px;border-radius:10px;cursor:pointer;}

.wrap{height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center;gap:40px;}
.wrap>.input{text-align:center;font-weight:bold;}
.wrap>.input>div,.wrap>.input>div.default{position:relative;padding-right:30px;box-sizing:border-box;background:#fff;border:1px solid #000;border-radius:20px;}
.wrap>.input>div.disable{background:#ededed;}
.wrap>.input>div>span{position:absolute;right:10px;top:50%;transform:translateY(-50%)}
.wrap>.input>div>input{width:4rem;font-size:30px;padding:10px;background:transparent;text-align:center;}

.wrap>.btns{display:flex;gap:30px;}
.wrap>.btns>button,.wrap>button.stop{font-size:1.5rem;}
.wrap>.btns>button.play{background:#f00}
